.section-container {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  background: url('../assets/image2.png') no-repeat center center/cover;
  color: white;
  padding: 50px;
  height: 100vh;
  position: relative; /* مهم لضمان حركة العناصر داخل الحاوية */
}

.content {
  max-width: 50%;
  padding: 20px;
  border-radius: 10px;
  text-align: right;
  position: relative; /* تأكد من أن المحتوى قابل للتحريك */
}

.title {
  font-size: 2rem;
  font-weight: bold;
  color: #00bcd4;
  margin-bottom: 10px; /* إضافة مسافة بين العنوان والنصوص الأخرى */
}

.subtitle {
  font-size: 1.2rem;
  margin: 10px 0;
  color: #a0a0a0;
}

.description {
  font-size: 1rem;
  margin-bottom: 20px;
  line-height: 1.5;
}

.cta-button {
  background-color: #00bcd4;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  border-radius: 5px;
  transition: background 0.3s;
  transform: scale(0); /* يمكن إضافة تحويلات لـ scale لإظهار الزر بشكل تدريجي */
}

.cta-button:hover {
  background-color: #008c9e;
  transform: scale(1); /* عند التمرير على الزر، يكبر تدريجيًا */
}
