.bg-primary {
  background-color: #f5f5f5; 
  width: 100%;
  overflow: hidden;
}

.paddingX {
  padding-left: 20px;
  padding-right: 20px;
}

.flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}

.flexStart {
  display: flex;
  justify-content: flex-start;
}

.boxWidth {
  width: 100%;
  max-width: 1200px; 
}


.paddingY {
  padding-top: 16px;
  padding-bottom: 16px;
}

.padding {
  padding: 16px 20px;
}

.marginX {
  margin-left: 16px;
  margin-right: 16px;
}

.marginY {
  margin-top: 16px;
  margin-bottom: 16px;
}

.flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}

.flexStart {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}

.heading2 {
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  font-size: 40px;
  color: white;
  line-height: 66.8px;
  width: 100%;
}

.heading2-xs {
  font-size: 48px;
  line-height: 76.8px;
}

.paragraph {
  font-family: 'Poppins', sans-serif;
  font-weight: normal;
  color: #d1d1d1; /* dim white */
  font-size: 18px;
  line-height: 30.8px;
}


.section {
  display: flex;
  flex-direction: column;
  padding-top: 16px;
  padding-bottom: 16px;
}

.sectionReverse {
  display: flex;
  flex-direction: column-reverse;
  padding-top: 16px;
  padding-bottom: 16px;
}

.sectionImgReverse {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
  margin-top: 10px;
  position: relative;
}

.sectionImg {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 10px;
  margin-top: 10px;
  position: relative;
}

.sectionInfo {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
}



/* شاشات أكبر من 1200px */
@media (max-width: 1200px) {
  .heading2 {
    font-size: 35px;
  }

  .paragraph {
    font-size: 16px;
  }

  .section {
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .sectionImg, .sectionImgReverse {
    margin: 5px;
  }

  .boxWidth {
    max-width: 100%; 
  }
}

/* شاشات أصغر من 1024px */
@media (max-width: 1024px) {
  .heading2 {
    font-size: 30px;
  }

  .paragraph {
    font-size: 14px;
  }

  .section {
    padding-top: 12px;
    padding-bottom: 12px;
  }

  .sectionInfo {
    padding: 0 15px; 
  }

  .sectionImg, .sectionImgReverse {
    margin: 0;
  }

  .boxWidth {
    max-width: 100%;
  }
}

/* شاشات أصغر من 768px */
@media (max-width: 768px) {
  .heading2 {
    font-size: 25px;
  }

  .paragraph {
    font-size: 13px;
  }

  .section {
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .sectionReverse, .section {
    flex-direction: column; 
  }

  .sectionInfo {
    padding: 0 10px;
  }

  .boxWidth {
    padding: 0 10px; /* تقليل الحشو */
  }
}

/* شاشات أصغر من 480px */
@media (max-width: 480px) {
  .heading2 {
    font-size: 22px;
  }

  .paragraph {
    font-size: 12px;
  }

  .section {
    padding-top: 8px;
    padding-bottom: 8px;
  }

  .sectionInfo {
    padding: 0 8px;
  }

  .boxWidth {
    padding: 0 8px;
  }
}
