.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 2rem; 
  background-color: rgba(10, 10, 35, 0.7); 
  color: white;
  position: fixed; 
  top: 0;
  left: 0;
  right: 0;
  z-index: 10; 
  height: 60px; 
}

.header__logo {
  display: flex;
  align-items: center;
}

.header__logo-image {
  height: 60px; 
  width: auto;
}

.header__nav {
  flex: 1;
  display: flex;
  justify-content: center;
}

.header__menu {
  list-style: none;
  display: flex;
  gap: 2rem;
}

.header__menu li a {
  text-decoration: none;
  color: white;
  font-size: 1rem;
  transition: color 0.3s;
}

.header__menu li a:hover {
  color: #00bcd4;
}


body {
  margin: 0;
  padding-top: 60px;
}
